
import headTop from "@/components/header";
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { mapGetters, mapMutations } from "vuex";
import Api from "@/js/common";
import single from "@/pages/questionType/single";
import double from "@/pages/questionType/double";
import downSelect from "@/pages/questionType/downSelect";
import singleLine from "@/pages/questionType/singleLine";
import multiLine from "@/pages/questionType/multiLine";

export default {
directives: {
TransferDom
},
data() {
return {
show1: false,
title: "详情",
endWord: "",
endTime: "",
questionList: [],
publish: "publishPop", //是否已发布
random: "", //随机数，提交使用
toastInfo: "",
ToastShow: false,
shareAdress: "",
describe: ""
};
},
mounted() {
appcan.setNavigationBarTitle({
title: "title"
});
},
computed: {
...mapGetters(["isLogin"]),
...mapGetters(["device"])
},
methods: {
getDetail() {
let vmSelf = this;
var submitObj = {
ques_id: vmSelf.$route.params.id,
ques_wj: vmSelf.$route.params.quesWj
};
vmSelf.$vux.loading.show({
text: "Loading"
});
Api.creatDetail(submitObj)
.then(function(response) {
console.log("detail------------>", response);
vmSelf.$vux.loading.hide();
vmSelf.title = response.ques.title;
vmSelf.endWord = response.ques.questionnaire;
vmSelf.endTime = response.ques.expire_date;
vmSelf.questionList = response.ques_bank;
vmSelf.random = response.ques.ques_wj;
vmSelf.describe = response.ques.descr;
vmSelf.shareAdress = response.url;
if (response.ques.status == 0) {

vmSelf.publish = "noPublishPop";
} else {

vmSelf.publish = "publishPop";
}
})
.catch(function(err) {
console.log("=====", err);
});
},
popAction(data) {
let vmSelf = this;
if (data == "delete") {
this.$vux.confirm.show({

title: "操作提示",
content: "确定删除吗？",
onCancel() {},
onConfirm() {
var submitObj = {
ques_id: vmSelf.$route.params.id,
value: 1
};
vmSelf.$vux.loading.show({
text: "Loading"
});
Api.quesDelete(submitObj)
.then(function(response) {
vmSelf.$vux.loading.hide();
vmSelf.toastInfo = response;
vmSelf.ToastShow = true;
setTimeout(function() {
vmSelf.$router.push("/index");
}, 3000);
})
.catch(function(err) {
console.log("=====", err);
});
}
});
} else if (data == "publish") {
var submitObj = {
ques_id: vmSelf.$route.params.id,
ques_wj: vmSelf.random
};
vmSelf.$vux.loading.show({
text: "Loading"
});
Api.quesShare(submitObj)
.then(function(response) {
if (response.status == 0) {
vmSelf.$vux.loading.hide();
vmSelf.toastInfo = response.msg;
vmSelf.ToastShow = true;
vmSelf.shareAdress = response.data;
vmSelf.show1 = true;
vmSelf.publish = "publishPop";
} else {
Api.errFn(1);
}
})
.catch(function(err) {
Api.errFn(2);
});
} else if (data == "edit") {
this.$router.push({
path: "/creatQuestion",
query: { id: vmSelf.$route.params.id, quesWj: vmSelf.random }
});
} else if (data == "share") {
vmSelf.show1 = true;
} else if (data == "investigate") {
this.$router.push({
path: `/investigate/${this.$route.params.id}/${
this.$route.params.quesWj
}`
});
}
},

changeTimeType(time) {
return Api.changeTimeType(time);
}
},
components: {
ViewBox,
headTop,
single,
double,
downSelect,
singleLine,
multiLine,
Popup,
Group,
Cell,
Toast,
XButton
},
created() {
this.getDetail();
}
};



